import { BasicColumn } from '/@/components/Table';
import { defHttp } from '/@/utils/http/axios';
import { useUserStore } from '/@/store/modules/user';

export const columns: BasicColumn[] = [
  {
    title: '所属公司',
    dataIndex: 'stationOrganName',
    width: 150,
    resizable: true,
    headAlign: 'center',
    align: 'left',
    customCell: (record) => ({
      rowSpan: record.rowOrgSpan,
    }),
    ifShow: useUserStore().isMultiOrganization,
  },
  {
    title: '电站名称',
    dataIndex: 'stationName',
    width: 150,
    headAlign: 'center',
    align: 'left',
    resizable: true,
  },
  {
    title: '计划停运',
    dataIndex: '',
    resizable: true,
    children: [
      {
        title: '停运次数',
        dataIndex: 'planNum',
        headAlign: 'center',
        align: 'right',
        width: 100,
        resizable: true,
      },
      {
        title: '停运时长(小时)',
        dataIndex: 'planHour',
        width: 100,
        headAlign: 'center',
        align: 'right',
        customRender: ({ text, record }) => {
          return record.planHour.toFixed(2);
        },
        resizable: true,
      },
    ],
  },
  {
    title: '非计划停运',
    dataIndex: '',
    resizable: true,
    children: [
      {
        title: '停运次数',
        dataIndex: 'unPlannedNum',
        headAlign: 'center',
        align: 'right',
        width: 100,
        resizable: true,
      },
      {
        title: '停运时长(小时)',
        dataIndex: 'unPlannedHour',
        width: 100,
        headAlign: 'center',
        align: 'right',
        customRender: ({ text, record }) => {
          return record.unPlannedHour.toFixed(2);
        },
        resizable: true,
      },
    ],
  },
  {
    title: '停运总时长(小时)',
    dataIndex: 'sumHour',
    width: 150,
    headAlign: 'center',
    align: 'right',
    customRender: ({ text, record }) => {
      return (record.planHour + record.unPlannedHour).toFixed(2);
    },
    resizable: true,
  },
  {
    title: '日报停运损失电量(kWh)',
    headAlign: 'center',
    align: 'right',
    dataIndex: 'sumLossElec',
    width: 150,
    resizable: true,
  },
  {
    title: '占比本期上网电量',
    dataIndex: '',
    width: 150,
    headAlign: 'center',
    align: 'right',
    customRender: ({ text, record }) => {
      if (record.sumOnlineElec != 0) {
        return ((record.sumLossElec / record.sumOnlineElec) * 100).toFixed(2) + '%';
      }
    },
    resizable: true,
  },
];

export enum Api {
  EXPORT_EXCEL_URL = '/bi/maintenance/export-excel',
}

// 导出工单统计列表
export const exportExcel = (params) =>
  defHttp.download(
    {
      url: Api.EXPORT_EXCEL_URL,
      params,
    },
    { isReturnNativeResponse: true },
  );
